<template>
    <div class="banner_wrap2 p5">
        <!-- <el-carousel :interval="4000" type="card" height="200px">
            <el-carousel-item v-for="item in 6" :key="item">
                <h3 class="medium">111</h3>
            </el-carousel-item>
        </el-carousel> -->
        <div class="wp1360 hheader" style="padding: 0 20px;">
            <h3></h3>
            <div class="dec_box">

                <div></div>
                <div>
                    <img src="@/assets/images/banners/l.png" alt="" width="20" @click="prev">
                    <img src="@/assets/images/banners/r.png" alt="" width="20" @click="next">

                </div>
            </div>
        </div>
        <el-carousel :interval="5000" :height="carouselHeight" :initial-index="banner_index" type="" arrow="nerver" indicator-position="none" ref="banner1">
            <el-carousel-item v-for="(item, index) in swiperList" :key="index">
                <div class="inner">
                    <el-image :src="item.imageUrl" alt="" style="width:100%;" fit="cover" @error="handleImageError" />
                    <div class="dec">
                        <h3 class="header">{{item.title}}</h3>
                        <div style="line-height:24px;">{{item.dec}}</div>
                    </div>
                </div>

            </el-carousel-item>

        </el-carousel>

    </div>
</template>

<script>
import { getSwiperList } from '@/api/index.js' // API

export default {
    name: 'Banner',
    components: {},
    props: {
        type: 0,
        blist: [],
    },
    watch: {},
    data() {
        return {
            banner_index: 0,
            swiperList: [
                {
                    imageUrl: require('@/assets/images/banners/bb1.jpg'),
                    index: 0,
                    title: 'CMMI 3',
                    dec: 'CMMI 3',
                },
                {
                    imageUrl: require('@/assets/images/banners/bb2.jpg'),
                    index: 1,
                    title: '',
                    dec: 'CMMICMMI30%40%',
                },
                {
                    imageUrl: require('@/assets/images/banners/bb3.jpg'),
                    index: 3,
                    title: 'CMMI 5',
                    dec: 'CMMI 5',
                },
                {
                    imageUrl: require('@/assets/images/banners/bb5.jpg'),
                    index: 3,
                    title: 'CMMI 5',
                    dec: 'CMMI 5',
                },
            ],
            carouselHeight: '380px', // 
        }
    },

    mounted() {
        if (this.blist && this.blist.length) {
            console.log('hasblist')
            this.swiperList = [...this.blist]
        }
        window.addEventListener('resize', this.setCarouselHeight)
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.setCarouselHeight)
    },
    methods: {
        prev() {
            this.$refs.banner1.prev()
        },
        next() {
            this.$refs.banner1.next()
        },
        handleImageError(event) {
            console.error(':', event.target.src)
            // event.target.src = require('@/assets/banner/default.jpg') // 
        },
        setCarouselHeight() {
            if (window.innerWidth <= 768) {
                this.carouselHeight = '200px' // 200px
            } else {
                this.carouselHeight = '500px'
            }
        },
    },
}
</script>

<style lang="less" scoped>
.inner {
    background: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);

    .dec {
        padding: 20px 32px 30px;

        h3 {
            font-size: 14px;
            margin: 0 0 10px;
        }
        div {
            font-size: 14px;
            color: #666;
            margin-bottom: 20px;
        }
    }
}
.banner_wrap2 {
    background: #f5f6f7;
    position: relative;
    padding: 20px 0;
    .hheader {
        h3 {
            font-size: 20px;
        }
        .dec_box {
            color: #666;
            margin-bottom: 20px;
            font-size: 14px;
            margin-top: 15px;
            display: flex;
            justify-content: space-between;
            img {
                cursor: pointer;
            }
        }
    }
    .banner_info {
        position: absolute;
        padding: 20px;
        right: 0;
        bottom: 0;
        z-index: 999;
        height: 230px;
        color: #fff;
        .big_font {
            font-size: 20px;
        }
        .read_more {
            position: absolute;
            bottom: 30px;
            right: 100px;
            cursor: pointer;
        }
    }
}
.banner_box {
    img {
        width: 100%;
        height: 600px;
    }
}
.banner {
    width: 100%;
    overflow: hidden;
}

.el-carousel {
    width: 100%;
}

.el-carousel__item {
    display: flex;
    justify-content: center;
    align-items: center;
}

.el-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media (max-width: 768px) {
    .el-image {
        object-position: center; // 
    }
}
/deep/ .el-carousel__container {
    height: 330px !important;
}
</style>